import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ArticleComponent } from '../article/article.component';
import { SinglePost } from '../models/single-post';
import { MediaService } from '../services/media.service';
import { UserService } from '../services/user.service';


@Component({
  selector: 'app-article-details',
  templateUrl: './article-details.component.html',
  styleUrls: ['./article-details.component.css']
})

export class ArticleDetailsComponent implements OnInit {
  recData!: SinglePost;
  isAuthor: any;
  userId: any;


  constructor(private art: ArticleComponent,private service: UserService, private route: ActivatedRoute, private media: MediaService) { }

  async ngOnInit(): Promise<void> {
    this.userId = localStorage.getItem('userId');
    this.recData = Object.assign(await this.media.getSingleRecord(this.route.snapshot.params.id, 'posts'));
    this.isAuthor = this.userId === this.recData.ownerId;
  }

}
